// g_snippetName
import { createSlice } from "@reduxjs/toolkit";
import { CURRENT_TOOL } from "../../content-web-constants";
import { setReactGlobalVariables } from "../../content-web-commonLib";

const initialState = CURRENT_TOOL.PERIPHERAL;

const r_currentTool = createSlice({
    name: "r_currentTool",
    initialState,
    reducers: {
        setCurrentTool(state, action) {
            state = action.payload;
            setReactGlobalVariables("g_currentTool", state);
            return state;
        }
    }
});

export const { setCurrentTool} = r_currentTool.actions;
export default r_currentTool.reducer;
