// g_partNumber_type
import { createSlice } from "@reduxjs/toolkit";
import { DEFAULT_VALUE } from "../../content-web-constants";
import { setReactGlobalVariables } from "../../content-web-commonLib";

const { CHIP_TYPE, PART_NUMBER } = DEFAULT_VALUE;
const initialState = PART_NUMBER[CHIP_TYPE].type;

const r_partNumber_type = createSlice({
    name: "r_partNumber_type",
    initialState,
    reducers: {
        set_partNumber_type(state, action) {
            state = action.payload;
            setReactGlobalVariables("g_partNumber_type" , state);
            return state;
        }
    }
});

export const { set_partNumber_type } = r_partNumber_type.actions;
export default r_partNumber_type.reducer;
