EXPORT_REACT.replacejscssfile = replacejscssfile;
EXPORT_REACT.getPropertyNames = getPropertyNames;
EXPORT_REACT.decideNewChipType = decideNewChipType;
EXPORT_REACT.decideNewPartNumberType = decideNewPartNumberType;
EXPORT_REACT.decideNewPartNumberPkg = decideNewPartNumberPkg;

