function initializeAll() {
    setReactGlobalVariables('g_groupIds', {});
    setReactGlobalVariables('g_recordedIds', {});
    setReactGlobalVariables('g_perControls', {});
    setReactGlobalVariables('g_enabledPeripheralFunctionData', {});

    g_perControlsAutoTest = {};
    g_perControlsFilter = {};

    g_module_snippet_txt = {};

    g_perControls_pin = {};
    g_perControls_clock = {};
}

function decidePerControlsV2 (text, partNumber_package, snippetName, callback) {
    var newReadSnippetFile = text,
        newControlConfig,
        newControl,
        newControlName = partNumber_package.slicePriorToX('(').toString(),
        newControlType = decideNewPartNumberType(partNumber_package),
        newControlFilter = {},
        newControlFilterExp,
        tempString,
        tempString1,
        tempArray = [],
        whileCount = 0,
        whileCount1 = 0,
        whileCount2 = 0,
        val,
        bfound,
        filterExp,
        filterExp1,
        realField,
        decideFieldFromFilter;

    decideFieldFromFilter = function (originalField, filter) {
        bfound = false;
        for (filterExp in originalField) {
            if (originalField.hasOwnProperty(filterExp)) {
                if (filterExp.indexOf('NUC_OR') === 0) {
                    realField = [];
                    for (filterExp1 in originalField[filterExp]) {
                        if (originalField[filterExp].hasOwnProperty(filterExp1)) {
                            try { newControlFilterExp = compileExpression(filterExp1); } catch (err) {
                                if (!g_bReadyForRelease) { alert(filterExp1 + ' failed at compileExpression. Something was wrong in decideFieldFromFilter.'); }
                            }
                            if (newControlFilterExp(filter)) {
                                bfound = true;
                                realField.push(originalField[filterExp][filterExp1]);
                            }
                        }
                    }
                    if (bfound) {
                        realField = realField.map(String);
                        break; // leave for (filterExp in originalField) {
                    }

                }
                else if (filterExp.indexOf('NUC_AND') === 0) {
                    bfound = true;
                    realField = [];
                    for (filterExp1 in originalField[filterExp]) {
                        if (originalField[filterExp].hasOwnProperty(filterExp1)) {
                            try { newControlFilterExp = compileExpression(filterExp1); } catch (err) {
                                if (!g_bReadyForRelease) { alert(filterExp1 + ' failed at compileExpression. Something was wrong in decideFieldFromFilter.'); }
                            }
                            if (newControlFilterExp(filter)) {
                                realField.push(originalField[filterExp][filterExp1]);
                            }
                            else {
                                bfound = false;
                                realField = [];
                                break; // for (filterExp1 in originalField[filterExp]) {
                            }
                        }
                    }
                    if (bfound) {
                        realField = realField.map(String);
                        break; // leave for (filterExp in originalField) {
                    }
                }
                else {
                    try { newControlFilterExp = compileExpression(filterExp); } catch (err) {
                        if (!g_bReadyForRelease) { alert(filterExp + ' failed at compileExpression. Something was wrong in decideFieldFromFilter.'); }
                    }
                    if (newControlFilterExp(filter)) {
                        bfound = true;
                        realField = originalField[filterExp].map(String);
                        break; // leave for (filterExp in originalField) {
                    }
                }
            }
        }
        if (!bfound) {
            if (typeof originalField.NUC_ANY !== 'undefined') {
                realField = originalField.NUC_ANY.map(String);
            }
            else {
                realField = 'none';
            }
        }

        return realField;
    };

    // g_groupIds[snippetName] = [];
    // g_recordedIds[snippetName] = [];
    // g_perControls[snippetName] = [];
    g_perControlsFilter[snippetName] = [];
    setReactGlobalVariables(`g_groupIds['${snippetName}']`, []);
    setReactGlobalVariables(`g_recordedIds['${snippetName}']`, []);
    setReactGlobalVariables(`g_perControls['${snippetName}']`, []);

    g_perControls_pin[snippetName] = null;
    g_perControls_clock[snippetName] = null;

    while (newReadSnippetFile.indexOf('<!') !== -1) {
        newControlConfig = newReadSnippetFile.sliceBetweenXandX('<!', '!>').replace(/\r?\n|\r/g, '');
        newControl = {};
        whileCount1 = 0;
        while (newControlConfig.indexOf(';') !== -1) {
            tempString = newControlConfig.slicePriorToX(';').trim();
            tempString1 = tempString.sliceAfterX(':').trim();
            tempString = tempString.slicePriorToX(':').replace(/\s/g, '');

            if (tempString1.indexOf('{') !== -1 && tempString1.indexOf('}') !== -1) {
                newControl[tempString] = JSON.parse(tempString1.replace(/'/g, '"').trim());
            }
            else if (tempString1.indexOf('[') === -1) {
                if (tempString.indexOf('filterExp') === -1) {
                    newControl[tempString] = tempString1.replace(/"/g, " ").trim();
                }
                else {
                    newControl[tempString] = tempString1.trim();
                }
            }
            else {
                if (tempString1.indexOf('"') === -1) {
                    tempString1 = tempString1.sliceAfterX('[');
                    tempArray = [];

                    whileCount2 = 0;
                    while (tempString1.indexOf(',') !== -1 || tempString1.indexOf(']') !== -1) {
                        if (tempString1.indexOf(',') !== -1) {
                            tempArray.push(tempString1.slicePriorToX(',').toString().trim());
                            tempString1 = tempString1.sliceAfterX(',');
                        }
                        else {
                            tempArray.push(tempString1.slicePriorToX(']').toString().trim());
                            tempString1 = "";
                        }

                        if (whileCount2 > 2000) {
                            break;
                        }
                        else {
                            whileCount2 = whileCount2 + 1;
                        }
                    }
                }
                else {
                    tempArray = JSON.parse(tempString1);
                }

                newControl[tempString] = tempArray;
            }

            newControlConfig = newControlConfig.sliceAfterX(';');
            if (whileCount1 > 2000) {
                break;
            }
            else {
                whileCount1 = whileCount1 + 1;
            }
        }

        // [Workaround] Use default instead of data, because data is not 100% correct.
        newControl.data = newControl.default;

        // handle the requirement for pin and clock
        if (typeof newControl.pin !== 'undefined') {
            if (typeof newControl.pin[snippetName] !== 'undefined') {
                g_perControls_pin[snippetName] = newControl.pin[snippetName];
            }
            if (typeof newControl.pin[newControlName] !== 'undefined') {
                g_perControls_pin[snippetName] = newControl.pin[newControlName];
            }
            if (g_perControls_pin[snippetName] === null) {
                g_perControls_pin[snippetName] = newControl.pin;
            }
        }

        if (typeof newControl.clock !== 'undefined') {
            if (typeof newControl.clock[snippetName] !== 'undefined') {
                g_perControls_clock[snippetName] = newControl.clock[snippetName];
            }
            if (typeof newControl.clock[newControlName] !== 'undefined') {
                g_perControls_clock[snippetName] = newControl.clock[newControlName];
            }
            if (g_perControls_clock[snippetName] === null) {
                g_perControls_clock[snippetName] = newControl.clock;
            }
        }

        // filter out tags
        if (typeof newControl.filter !== 'undefined') {
            newControlFilter = newControl.filter;
        }
        if (typeof newControlFilter[newControlName] !== 'undefined') {
            if (typeof newControlFilter[newControlName] == "function") {
                newControlFilter = newControlFilter[newControlName]();
            }
            else {
                newControlFilter = newControlFilter[newControlName];
            }
        }
        else if (typeof newControlFilter[newControlType] !== 'undefined') {
            if (typeof newControlFilter[newControlType] == "function") {
                newControlFilter = newControlFilter[newControlType]();
            }
            else {
                newControlFilter = newControlFilter[newControlType];
            }
        }
        newControlFilter.name = newControlName;
        if (newControlType !== "") {
            newControlFilter.type = newControlType;
        }
        for (val in g_supportedGPIOPins) {
            if (g_supportedGPIOPins.hasOwnProperty(val)) {
                newControlFilter[val] = g_supportedGPIOPins[val];
            }
        }
        // AutoTest should not consider filterExp. EX: M251 USCI0_UART UUART0_UARTWakeupSourceCheckbox
        if (!NUTOOL_PER.g_bFunctionalTesting && typeof newControl.filterExp !== 'undefined' &&
            newControl.filterExp !== 'none' &&
            newControl.filterExp !== '') {
            try { newControlFilterExp = compileExpression(newControl.filterExp); } catch (err) {
                if (!g_bReadyForRelease) { alert(newControl.id + ' failed at compileExpression. Something was wrong in filterExp.'); }
            }
            if (!newControlFilterExp(newControlFilter)) {
                // Save configuration need those info, so store to g_perControlsFilter.
                g_perControlsFilter[snippetName].push({
                    "id": newControl.id,
                    "data": newControl.data,
                });

                newControl = {};
            }
        }

        if (!isEmptyObject(newControl)) {
            if (typeof newControl.default !== 'undefined' &&
                isObject(newControl.default) && !Array.isArray(newControl.default)) {
                newControl.default = decideFieldFromFilter(newControl.default, newControlFilter);
            }
            if (typeof newControl.enum !== 'undefined' &&
                isObject(newControl.enum) && !Array.isArray(newControl.enum)) {
                newControl.enum = decideFieldFromFilter(newControl.enum, newControlFilter);
            }
            if (typeof newControl.optionLabels !== 'undefined' &&
                isObject(newControl.optionLabels) && !Array.isArray(newControl.optionLabels)) {
                newControl.optionLabels = decideFieldFromFilter(newControl.optionLabels, newControlFilter);
            }

            // g_perControls[snippetName].push(newControl);
            setReactGlobalVariables(`g_perControls['${snippetName}']`, newControl, true);
        }

        newReadSnippetFile = newReadSnippetFile.sliceAfterX('>');
        if (whileCount > 2000) {
            break;
        }
        else {
            whileCount = whileCount + 1;
        }
    }
    g_enabledPeripheralFunctionData = getGlobalVariables("NUTOOL_PER", "g_enabledPeripheralFunctionData");
    var selControl= g_enabledPeripheralFunctionData[snippetName];
    if(typeof selControl !== "undefined"){
        for (let key in selControl) {
            let newData = selControl[key];
            let controls = g_perControls[snippetName];
            for(let obj of controls){
                if (key === obj.id) {
                    obj["data"] = newData;
                }
                if(key === obj.observable) {
                    obj["data"] = obj["listener"][newData];
                }
            }
        }
    }

    if (!g_bReadyForRelease && window.console){window.console.log("In decidePerControlsV2, g_perControls.length:" + g_perControls[snippetName].length); }
    if(typeof callback === "function"){
        callback();
    }
};

// Pre-process for external.generatePackage_ncfg = function()
function concatenate_generated_cfg() {
    var snippetNames = g_cfg_perFunctions;
    var result = {}; // Similar to g_generatedCodeFromPerString
    g_enabledPeripheralFunctionData = getGlobalVariables("NUTOOL_PER", "g_enabledPeripheralFunctionData");

    for(let val of snippetNames){
        if(g_perControls.hasOwnProperty(val)) {
            result[val] = {};
            for(let objFilter of g_perControlsFilter[val]){
                result[val][objFilter.id] = objFilter.data; 
            }
            for(let obj of g_perControls[val]){
                if(obj.hasOwnProperty("id") && obj.hasOwnProperty("data")){
                    result[val][obj.id] = obj.data;
                }
            }
        }
        else {
            result[val] = g_enabledPeripheralFunctionData[val];
        }
    }

    g_generatedCodeFromPerString = JSON.stringify(result);
    setGlobalVariables("NUTOOL_PER", "g_generatedCodeFromPerString");
    g_enabledPeripheralFunctionString = getPropertyNames(result).join('/');
    setGlobalVariables("NUTOOL_PER", "g_enabledPeripheralFunctionString");
    return g_generatedCodeFromPerString;
}

function prepare_generated_code(peripherals=g_cfg_perFunctions) {
    const replaceValueByTag = function (text="", replaceTag, replaceVal) {
        replaceVal = replaceVal.toString();
        replaceTag = replaceTag + ":";
        text = text.split(replaceTag);
        if(text.length === 2) {
            if (replaceVal.includes(",")) {
                replaceVal = "[" + replaceVal.replace(/,/g, ", ").trim() + "]";
            }
            // [Workaround] Force to change value
            else if(replaceVal === "true"){
                replaceVal = "1";
            }
            else if(replaceVal === "false" || replaceVal.trim() === ""){
                replaceVal = "0";
            }
            text[1] = replaceVal + ";" + text[1].sliceAfterX(";");
        }
        return text.join(replaceTag);
    };

    const modifyPeripheralTxt = function (text="", peripheral) {
        // Modify .txt by g_generatedCodeFromPerString
        var t_perControl = JSON.parse(g_generatedCodeFromPerString)[peripheral];

        // var separatorDefine = "#define ";
        var separatorTail = ";";
        var separatorId = "<!id:";
        var textAry = text.split(separatorId);
        for(let i=0 ; i<textAry.length ; i++ ) {
            let id = textAry[i].slicePriorToX(separatorTail);
            if(isString(id) && t_perControl && t_perControl[id] !== undefined){
                if(id.toLowerCase().includes("textonly") ){
                    textAry[i] = replaceValueByTag(textAry[i], "default", "");
                }
                else {
                    textAry[i] = replaceValueByTag(textAry[i], "default", t_perControl[id]);
                }
            }
        }
        return textAry.join(separatorId);
    };

    const handleMultiId = function (text="", peripheral) {
        var t_perControl = g_perControls[peripheral];
        var t_filterIdAry = g_perControlsFilter[peripheral].map(x => x.id);
        var separatorDefine = "#define ";
        var separatorTail = ";";
        var separatorId = "<!id:";
        var textDefineAry = text.split(separatorDefine);

        for(let i=0 ; i<textDefineAry.length ; i++ ) {
            let textIdAry = textDefineAry[i].split(separatorId);
            if (textIdAry.length > 2){
                let showIdAry = [], hideIdAry = [];
                 // textIdAry[0] is #define NUCODEGEN_ ...
                for(let j=1; j<textIdAry.length; j++){
                    let id = textIdAry[j].slicePriorToX(separatorTail);
                    let dependencies = getValueByTag(textIdAry[j], "dependencies");
                    let dependenciesOption = getValueByTag(textIdAry[j], "dependenciesOption");

                    // EX: M031 #define NUCODEGEN_I2C0_PDMA_EN (<!id:i2c0_pdma_en ... !><!id:i2c0_pdma1_en ... !>)
                    // eslint-disable-next-line no-restricted-globals
                    if(NUTOOL_PER.g_bFunctionalTesting && external.headerGenerator_tagId && external.headerGenerator_tagId !== id){
                        hideIdAry.push(id);
                    }
                    else if (confirmDependenciesValid(dependencies, dependenciesOption, t_perControl)){
                        if(!t_filterIdAry.includes(id)){
                            showIdAry.push(id);
                        }
                        else {
                            hideIdAry.push(id);
                        }
                    }
                    else {
                        hideIdAry.push(id);
                    }
                }

                // hideIdAry must keep one
                if (showIdAry.length === 0) {
                    hideIdAry.pop();
                }
                // showIdAry must keep one
                else if(showIdAry.length > 1) {
                    showIdAry.pop();
                    hideIdAry = hideIdAry.concat(showIdAry);
                }
                // ideal(showIdAry.length = 1) 
                else {
                    // NOOP
                }

                // Ensure a #define contain a <!id
                for(let id of hideIdAry) {
                    text = NUTOOL_PER.replaceTag(text, id, "NUC_NULL", "entireTag");
                }
            }
        }

        // [Workaround] Handle filterDefault of removeAllTags() in NUC_Peripheral_Configuration.js
        for(let control of t_perControl){
            let { id, data, filterDefault } = control;

            if (filterDefault === "false"){
                text = NUTOOL_PER.replaceTag(text, id, data, "entireTag");
            }
        }

        return text;
    };

    const getValueByTag = function(text="", tag) {
        tag = tag.endsWith(":") ? tag : (tag + ":");
        let result = text.sliceAfterX(tag).slicePriorToX(";");
        if(result.match(/[{}]/g)){
            return JSON.parse(result);
        }
        else if (result.startsWith('[')){
            return result.slice(1, -1).split(",").map(x => x.trim());
        }
        else {
            return result.trim();
        }
    }

    const promises = peripherals.map(peripheral => {
        return new Promise(resolve => {
            if(g_module_snippet_txt[peripheral] && g_perControlsAutoTest[peripheral]) {
                resolve(g_module_snippet_txt[peripheral])
            }
            else {
                var url = "PeripheralConfigure\\" + g_chipType + "\\" + peripheral + '.txt';
                // [Workaround] app.asar.unpacked
                var cwd = window.__dirname || "";
                if(cwd.includes("\\app.asar.unpacked\\")) {
                    url = cwd + "\\" + url;
                }

                fetch(url)
                .then(response => response.text())
                .then(text =>
                    g_module_snippet_txt[peripheral] = text
                )
                .then(text => {
                    decidePerControlsV2(
                        text.replace(/\r/g, "").replace(/\/\*[\s\S]*?\*\/|\/\/.*/g, '').replace(/<br>/g, ""),
                        g_partNumber_package,
                        peripheral
                    )
                    // [Workaround] This case can not use Object.assign()
                    g_perControlsAutoTest[peripheral] = JSON.parse(JSON.stringify(g_perControls[peripheral]));
                    return text;
                })
                .then(text => {
                    resolve(text)
                })
            }
        })
        .then(text => {
            return modifyPeripheralTxt(text, peripheral);
        })
        .then(text => {
            return handleMultiId(text, peripheral);
        })
        .then(text => {
            g_module_snippet_code[peripheral] = NUTOOL_PER.removeAllTags(text);
        })
        .then(() => {
            // console.log(peripheral);
        });
    });
    g_module_snippet_code = [];
    concatenate_generated_cfg();
    return Promise.all(promises)
        .then(() => {
            setGlobalVariables("NUTOOL_PER", "g_module_snippet_code");
        });
};

// Pre-process for gemerate periph_conf.h
async function concatenate_generated_code(callback) {
    await prepare_generated_code();
    var local_module_snippet_code = ""

    g_generatedCodeFromPerString = "";
    g_enabledPeripheralFunctionData = getGlobalVariables("NUTOOL_PER", "g_enabledPeripheralFunctionData");

    if (true || NUTOOL_PER.g_perFunctionString === "periph_conf.h") {
        local_module_snippet_code = "// MACROs above generated by the NuCodeGenerator tool\n\n";
    }
    if (NUTOOL_PER.g_bFunctionalTesting) {
        local_module_snippet_code += "#define NUCODEGEN_UTEST 1\n\n";
    }

    var moduleNames = getPropertyNames(g_module_snippet_primitive);
    for(let module of moduleNames) {
        for(let snippet of g_module_snippet_primitive[module]){
            // EX: GPIO_DB's modeuleName is GPIO
            if(snippet.includes("_")){
                let moduleName = snippet.split("_")[0];
                let bExist = g_generatedCodeFromPerString.includes("#define NUCODEGEN_" + moduleName + " ");
                if (!bExist) {
                    if(g_cfg_perFunctions.map(x => x.split("_")[0]).includes(moduleName)) {
                        g_generatedCodeFromPerString += "#define NUCODEGEN_" + moduleName + " 1\n";
                    }
                    else {
                        g_generatedCodeFromPerString += "#define NUCODEGEN_" + moduleName + " 0\n";
                    }
                }
            }

            // EX: CLK
            if(g_cfg_perFunctions.includes(snippet)) {
                g_generatedCodeFromPerString += "#define NUCODEGEN_" + snippet + " 1\n";
            }
            else {
                g_generatedCodeFromPerString += "#define NUCODEGEN_" + snippet + " 0\n";
            }

            if(g_module_snippet_code[snippet]) {
                local_module_snippet_code += g_module_snippet_code[snippet] + "\n";
            }
        }
    }

    g_generatedCodeFromPerString = g_generatedCodeFromPerString + "\n" + local_module_snippet_code;
    setGlobalVariables("NUTOOL_PER", "g_generatedCodeFromPerString");
    if(typeof callback === 'function'){
        callback();
    }
    return g_generatedCodeFromPerString;
}

function isString(object) {
    return typeof object === "string" ? true : false;
}

function isObject (object) {
    return typeof object === "object" ? true : false;
}

function isEmptyObject (object) {
    var str = JSON.stringify(object);
    return  (str === "[]" || str === "{}");
};


function getDefaultValue (data) {
    // Example: In M253/CLK.txt, if type is integer, but data can be 512 or [0].
    if(isObject(data)) {
        return data.length === 1 ? data[0] : data;
    }
    // Example: Handle type checkboxBoolean's data.
    else if(data === "true") {
        return true
    }
    else if(data === "false") {
        return false
    }
    else {
        return data;
    }
}

/**
 * Confirm one by one
 * @param {String} dependency Single target id
 * @param {String or Object} dependencyOption Its condition
 * @param {Array} local_perControls All peripheral controls
 * @param {Array} bShowTable Record bShow history for id.
 * @returns {Boolean}  true: Meet the condition / false
 */
function confirmDependencyValid(dependency, dependencyOption, local_perControls, bShowTable) {
    function confirmDependencyOptionValid(dependencyOption, data) {
        let bMatch = false;
        dependencyOption = getDefaultValue(dependencyOption);
        data = getDefaultValue(data)
        if (String(data) === String(dependencyOption)) {
            bMatch = true;
        }
        // EX: [UART_INTEN_RDAIEN_Msk,UART_INTEN_THREIEN_Msk]
        // EX: {"WWDT_PrescalerSelect":[
        //     "WWDT_PRESCALER_4",    "WWDT_PRESCALER_8",   "WWDT_PRESCALER_16",   "WWDT_PRESCALER_32",
        //     "WWDT_PRESCALER_64",   "WWDT_PRESCALER_128", "WWDT_PRESCALER_192",  "WWDT_PRESCALER_256",
        //     "WWDT_PRESCALER_384",  "WWDT_PRESCALER_512", "WWDT_PRESCALER_768",  "WWDT_PRESCALER_1024",
        //     "WWDT_PRESCALER_1536", "WWDT_PRESCALER_2048"]}
        else if(isObject(dependencyOption)) {
            bMatch = dependencyOption.includes(data);
        }
        else if(isString(dependencyOption)) {
            let bIncludesOr = dependencyOption.includes("|");
            let bIncludesAnd = dependencyOption.includes("&");
            // EX: ==512
            if (dependencyOption.startsWith("==")){
                bMatch = (data === dependencyOption.substring(2)) ? true : false;
            }
            // else if (dependencyOption.startsWith(">=")){
            //     bMatch = (data >= dependencyOption.substring(2)) ? true : false;
            // }
            // EX: <=16
            else if (dependencyOption.startsWith("<=")){
                bMatch = (data <= dependencyOption.substring(2)) ? true : false;
            }
            // EX: >16
            else if (dependencyOption.startsWith(">")){
                bMatch = (data > dependencyOption.substring(1)) ? true : false;
            }
            // EX: <512
            else if (dependencyOption.startsWith("<")){
                bMatch = (data < dependencyOption.substring(1)) ? true : false;
            }
            // else if (!bIncludesOr && bIncludesAnd) {
            //     dependencyOption = dependencyOption.split(/[&() ]/).filter(value => value !== "");
            //     bMatch = confirmDependencyOptionValid(dependencyOption, data);
            // }
            // EX: TIMER_ONESHOT_MODE|TIMER_PERIODIC_MODE|TIMER_CONTINUOUS_MODE;
            else if (bIncludesOr && !bIncludesAnd) {
                dependencyOption = dependencyOption.split(/[|() ]/).filter(value => value !== "");
                for(let value of dependencyOption){
                    if(confirmDependencyOptionValid(value, data)){
                        bMatch = true;
                    };
                }
            }
            // EX; (UART_INTEN_RDAIEN_Msk|UART_INTEN_THREIEN_Msk)&UART_INTEN_RLSIEN_Msk
            // else if (bIncludesOr && bIncludesAnd){
            //     bMatch = true;
            //     dependencyOption = dependencyOption.split("&");
            //     for(let value of dependencyOption){
            //         bMatch &= confirmDependencyOptionValid(value, data) 
            //     }
            // }
            else {
                // NOOP
            }
        }
        return bMatch;
    }

    let bMatch = false;
    let bNot = dependency.trim().startsWith("!");
    let tmpId = dependency.substring(bNot);

    // If id(in dependencies) is hidden, bypass to confirm dependenciesOption.
    if(bShowTable && !bShowTable[tmpId]){
        return Boolean(bNot^false);
    }
    for(var obj of local_perControls){
        if(tmpId === obj.id) {
            // Avoid dependencyOption[tmpId] === 0
            if(dependencyOption[tmpId] !== undefined){
                dependencyOption = dependencyOption[tmpId]
            }
            if(obj.type === "multipleselect" && isObject(obj.data)){
                bMatch = confirmDependencyOptionValid(dependencyOption, obj.data.sort().join(" | "));
            }
            else{
                bMatch = confirmDependencyOptionValid(dependencyOption, obj.data);
            }
            // Based on id is unique.
            break;
        }
    }
    return Boolean(bNot^bMatch);
}

/**
 * Confirm this peripheral control's all dependencies
 * @param {String or Array} dependencies 
 * @param {String or Object} dependenciesOption 
 * @param {Array} local_perControls 
 * @param {Array} bShowTable Record bShow history for id.
 * @returns {boolean} true: Show(Meet all conditions) / false: Hide
 */
function confirmDependenciesValid(dependencies, dependenciesOption, local_perControls, bShowTable) {
    let bShow = true;

    // EX: [CLK_PowerDownEnableRadio, CLK_PowerDownModeSelect]
    if (typeof dependencies === "object") {
        for(let value of dependencies){
            if(!confirmDependencyValid(value, dependenciesOption, local_perControls, bShowTable)) {
                bShow = false;
            };
        }
    }
    else if (typeof dependencies === "string") {
        let bIncludesOr = dependencies.includes("|");
        let bIncludesAnd = dependencies.includes("&");

        // EX: UsePWM0CH0AdvanceBoolean&UsePWM0CH1AdvanceBoolean
        if (!bIncludesOr && bIncludesAnd) {
            // should redirect to case "typeof dependencies === object"
            dependencies = dependencies.split(/[&() ]/).filter(value => value !== "");
            bShow = confirmDependenciesValid(dependencies, dependenciesOption, local_perControls, bShowTable);
        }
        // EX: UsePWM0CH0AdvanceCapBoolean|UsePWM0CH1AdvanceCapBoolean
        // EX: (UART0TimeoutCounterCheckbox | UART0TimeoutWakeupSelect)
        else if (bIncludesOr && !bIncludesAnd) {
            bShow = false;
            dependencies = dependencies.split(/[|() ]/).filter(value => value !== "");
            for(let value of dependencies){
                if(confirmDependencyValid(value, dependenciesOption, local_perControls, bShowTable)){
                    bShow = true;
                };
            }
        }
        // EX: (UseADCCMP0Boolean|ADCOperationModeSelect)&BPWM0SSRCRadio
        // EX: PWM0CH01OutputFuncSetmode&(UsePWM0CH0ENBoolean|UsePWM0CH1ENBoolean)
        else if (bIncludesOr && bIncludesAnd){
            dependencies = dependencies.split("&");
            for(let value of dependencies){
                bShow &= confirmDependenciesValid(value, dependenciesOption, local_perControls, bShowTable);
            }
        }
        // !bIncludesOr && !bIncludesAnd
        else if (dependencies !== "none" && dependencies !== "") {
            bShow = confirmDependencyValid(dependencies, dependenciesOption, local_perControls, bShowTable);
        }
        else {
            // NOOP
        }
    }
    else {
        // NOOP
    }
    return Boolean(bShow);
}

function compareHexNumber(val, max, min) {
    let _val = parseInt(val, 16);
    let _max = parseInt(max, 16);
    let _min = parseInt(min, 16);

    return (_val > _max) ? max : (_val < _min) ? min : val;
}

/**
 * Sync data
 * @param {String} variables
 * @param {*} newValue
 * @param {String} bPush if true, use g_xxx.push()
 */
function setReactGlobalVariables(variables, newValue, bPush=false) {
    let exeStr = "";
    try {
        if(bPush) {
            exeStr = `${variables}.push(newValue)`;
        }
        else{
            exeStr = `${variables}  =  newValue`;
        }
        EXPORT_REACT._D("[setReactGlobalVariables] " + exeStr.replace('newValue', `${newValue}`));

        // eslint-disable-next-line no-eval
        eval(exeStr);

        setGlobalVariables("NUTOOL_PER", variables);
    }
	catch (e) {
        console.error(e);
    }
};

/**
 * React sync value of global variable to specified scope
 * @param {String} scope NUTOOL_PER or NUTOOL_PIN or NUTOOL_CLOCK
 * @param {String} variables g_xxxx
 */
function setGlobalVariables(scope, variables) {
    switch(scope) {
        case "NUTOOL_PER":
             // eslint-disable-next-line no-eval
            NUTOOL_PER.setg_variables(variables, eval(`${variables}`));
            break;
        case "NUTOOL_PIN":
             // eslint-disable-next-line no-eval
            NUTOOL_PIN.setg_variables(variables, eval(`${variables}`));
            break;
        case "NUTOOL_CLOCK":
             // eslint-disable-next-line no-eval
            NUTOOL_CLOCK.setg_variables(variables, eval(`${variables}`));
            break;
        default:
            break;
    }
};

/**
 * React need to get value of global variable
 * @param {String} scope NUTOOL_PER or NUTOOL_PIN or NUTOOL_CLOCK
 * @param {String} variables g_xxxx 
 * @returns  value of global variable
 */
function getGlobalVariables(scope, variables) {
    let exeStr = `${scope}.getg_variables(variables)`;
    // eslint-disable-next-line no-eval
    return eval(exeStr)
};

function confirmModules() {
    g_module_snippet_pin = []
    g_module_snippet_clock = [];

    // handle g_module_snippet_pin
    for(let snippetName in g_perControls_pin){
        let localPin = g_perControls_pin[snippetName];
        if(localPin !== null && g_cfg_perFunctions.includes(snippetName)){
            g_module_snippet_pin[snippetName] = [];

            for(let key of getPropertyNames(localPin)){
                let bPush = false;
                let txtTagData, pinName = localPin[key];

                // key: "ALL"/txt tag id 
                if(key === "ALL"){
                    bPush = true;
                }
                else {
                    let matchTag = g_perControls[snippetName].filter(x => x.id === key)[0];
                    [txtTagData, pinName] = Object.entries(pinName)[0];
                    if(matchTag && Array.isArray(matchTag.data) && matchTag.data.includes(txtTagData)) {
                        bPush = true;
                    }
                    else if(matchTag && matchTag.data.toString() === txtTagData) {
                        bPush = true;
                    }
                }

                if(bPush) {
                    if (isString(pinName)) {
                        if(!g_module_snippet_pin[snippetName].includes(pinName)){
                            g_module_snippet_pin[snippetName].push(pinName);
                        }
                    }
                    else if (Array.isArray(pinName)) {
                        pinName.forEach(function(val) {
                            if(!g_module_snippet_pin[snippetName].includes(val)){
                                g_module_snippet_pin[snippetName].push(val);
                            }
                        });
                    }
                    else {
                        // [Workaround]
                        console.error("illegal g_module_snippet_pin");
                    }
                }
            }
        }
    }

    // handle g_module_snippet_clock
    for(let snippetName in g_perControls_clock){
        let localClock = g_perControls_clock[snippetName];
        if(localClock !== null && g_cfg_perFunctions.includes(snippetName)){
            g_module_snippet_clock[snippetName] = [];

            for(let key of getPropertyNames(localClock)){
                
                let bPush = false;
                // clockName: ""/"CRC"/["EBI"]/"UseLCDPORRadio" : {"LCD_VOLTAGE_SOURCE_CP" : "LCDCP"}
                let txtTagData, clockName = localClock[key];

                // key: "ALL"/txt tag id
                if(key === "ALL"){
                    bPush = true;
                }
                else {
                    let matchTag = g_perControls[snippetName].filter(x => x.id === key)[0];
                    [txtTagData, clockName] = Object.entries(clockName)[0];
                    if(matchTag && Array.isArray(matchTag.data) && matchTag.data.includes(txtTagData)) {
                        bPush = true;
                    }
                    else if(matchTag && matchTag.data.toString() === txtTagData) {
                        bPush = true;
                    }
                }

                if(bPush) {
                    if (isString(clockName)) {
                        if(!g_module_snippet_clock[snippetName].includes(clockName)){
                            g_module_snippet_clock[snippetName].push(clockName);
                        }
                    }
                    else if (Array.isArray(clockName)) {
                        clockName.forEach(function(val) {
                            if(!g_module_snippet_clock[snippetName].includes(val)){
                                g_module_snippet_clock[snippetName].push(val);
                            }
                        });
                    }
                    else {
                        // [Workaround]
                        console.error("illegal g_module_snippet_clock");
                    }
                }
            }
        }
    }
    setGlobalVariables("NUTOOL_PER", "g_module_snippet_pin");;
    setGlobalVariables("NUTOOL_PER", "g_module_snippet_clock");;
};

function drawCanvas() {
    try {
        g_object = {};
        g_object.clock = {};
        g_object.peripheral = {};
    
        let tempString = ":";
        let control = g_perControls[g_snippetName];
        if (!control) return;
        for (let i = 0, max = control.length; i < max; i += 1) {
            if (typeof control[i].id !== 'undefined') {
                tempString += control[i].id;
                if (i !== max - 1) {
                    tempString += '/:';
                }
                else {
                    tempString += '/';
                }
            }
        }
        g_object.peripheralAllIds = tempString;
    
        if (typeof NUTOOL_CLOCK !== 'undefined') {
            NUTOOL_CLOCK.getGeneratedClockStatus();
            g_object.clock = JSON.parse(NUTOOL_CLOCK.getg_clockRegsString1());
        }
    
        for (let i = 0, max = control.length; i < max; i += 1) {
            if (typeof control[i].id !== 'undefined') {
                let cmp = document.getElementById(control[i].id);
                if (cmp && cmp.checkVisibility()){
                    g_object.peripheral[control[i].id] = getDefaultValue(control[i].data);
                }
            }
        }

        if (NUTOOL_PER.drawCanvas("canvas_Peripheral", g_chipType, g_object, g_snippetName)) {
            $("#canvas_Peripheral").show();
        }
        else {
            $("#canvas_Peripheral").hide();
        }
    }
    catch(e){
        console.error("[drawCanvas] " + e);
    }
};