//
import { createSlice } from "@reduxjs/toolkit";
import { DEFAULT_VALUE } from "../../content-web-constants";
import { setReactGlobalVariables } from "../../content-web-commonLib";

const { CHIP_TYPE, PART_NUMBER } = DEFAULT_VALUE;
const initialState = PART_NUMBER[CHIP_TYPE].package;
g_partNumber_package = initialState;

const r_partNumber_package = createSlice({
    name: "r_partNumber_package",
    initialState,
    reducers: {
        set_partNumber_package(state, action) {
            state = action.payload;
            setReactGlobalVariables("g_partNumber_package", state);
            return state;
        }
    }
});

export const { set_partNumber_package } = r_partNumber_package.actions;
export default r_partNumber_package.reducer;
