// Select item from NUTOOL_PER.g_cfg_perFunctions
import { createSlice } from "@reduxjs/toolkit";

const initialState = [];

const r_cfg_perFunctions = createSlice({
    name: "r_cfg_perFunctions",
    initialState,
    reducers: {
        set_cfg_perFunctions(state, action) {
            state = action.payload;
            g_cfg_perFunctions = [...state].sort();
            return state;
        }
    }
});

export const { set_cfg_perFunctions } = r_cfg_perFunctions.actions;
export default r_cfg_perFunctions.reducer;
