import { combineReducers } from "redux";
import r_chipType from "../slices/r_chipType";
import r_snippetName from "../slices/r_snippetName";
import r_cfg_perFunctions from "../slices/r_cfg_perFunctions";
import r_partNumber_package from "../slices/r_partNumber_package";
import r_partNumber_type from "../slices/r_partNumber_type";
import r_currentTool from "../slices/r_currentTool";

const reducers = combineReducers({
  r_chipType,
  r_snippetName,
  r_cfg_perFunctions,
  r_partNumber_package,
  r_partNumber_type,
  r_currentTool,
});

export default reducers;