import React from 'react';
import ReactDOM from 'react-dom/client';
import { Provider } from 'react-redux';
import { Modal, notification } from 'antd';
import store from "./store";
import './index.css';
import './i18n'
// import App from './App';
import reportWebVitals from './reportWebVitals';

import Toolbar from './components/Toolbar';
import ChipSeries from './components/ChipSeries';
import PartNo from './components/PartNo';
import PeripheralFunctions from './components/PeripheralFunctions';
import LoadPeripheralTxt from './components/LoadPeripheralTxt';

const toHtmlContent = (content) => {
  if (typeof content === "string"){
    content = content.replaceAll("\n", "<br/>");
  }

  try {
    return <div dangerouslySetInnerHTML={{__html:`${content}`}}/>;
  }
  catch(e){
    console.error(e);
    return content;
  }
};

EXPORT_REACT._D = (msg) => {
  if (!g_bReadyForRelease) {
    console.warn(msg);
  }
};

EXPORT_REACT.notification_info = (title, content, duration) => notification.info({
  message: title || "Info",
  description: toHtmlContent(content),
  placement: 'bottomRight',
  duration: duration || null,
  style: {
    inlineSize: "max-content",
    minWidth: 530,
  },
});

EXPORT_REACT.notification_success = (title, content, duration) => notification.success({
  message: title || "Success",
  description: toHtmlContent(content),
  placement: 'bottomRight',
  duration: duration || null,
  style: {
    inlineSize: "max-content",
    minWidth: 530,
  },
});

EXPORT_REACT.notification_warning = (title, content, duration) => notification.warning({
  message: title || "Waning",
  description: toHtmlContent(content),
  placement: 'bottomRight',
  duration: duration || null,
  style: {
    inlineSize: "max-content",
    minWidth: 530,
  },
});

EXPORT_REACT.notification_error = (title, content) => notification.error({
  message: title || "Error",
  description: toHtmlContent(content),
  placement: 'bottomRight',
  duration: null,
  style: {
    inlineSize: "max-content",
    minWidth: 530,
  },
});

EXPORT_REACT.mask = (title, content) => Modal.confirm({
  title: title,
  content: content ? toHtmlContent(content) : "Executing, please wait...",
  footer: null,
  centered: true,
  width: "auto",
  style: {
    minWidth: 416,
  },
});

EXPORT_REACT.info = (title, content) => Modal.info({
  title: title,
  content: toHtmlContent(content),
  centered: true,
});

EXPORT_REACT.error = (title, content) => Modal.error({
  title: title,
  content: toHtmlContent(content),
  centered: true,
});

EXPORT_REACT.unmask = () => Modal.destroyAll();

const topToolBar = ReactDOM.createRoot(document.getElementById('topToolBar'));
topToolBar.render(
  <Provider store={store}>
    <Toolbar/>
  </Provider>
);

const ChipType_Peripheral = ReactDOM.createRoot(document.getElementById('ChipType_Peripheral'));
ChipType_Peripheral.render(
  <Provider store={store}>
    <ChipSeries />
  </Provider>
);

const MCU_Peripheral = ReactDOM.createRoot(document.getElementById('MCU_Peripheral'));
MCU_Peripheral.render(
  <Provider store={store}>
    <PartNo />
  </Provider>
);

const rootTree_Peripheral = ReactDOM.createRoot(document.getElementById('rootTree_Peripheral'));
rootTree_Peripheral.render(
  <Provider store={store}>
    <PeripheralFunctions />
  </Provider>
);

const CodeGeneratorContainer = ReactDOM.createRoot(document.getElementById('field1'));
CodeGeneratorContainer.render(
  <Provider store={store}>
    <LoadPeripheralTxt />
  </Provider>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
