// g_chipType
import { createSlice } from "@reduxjs/toolkit";
import { DEFAULT_VALUE } from "../../content-web-constants";
import { setReactGlobalVariables } from "../../content-web-commonLib";

const initialState = DEFAULT_VALUE.CHIP_TYPE;
g_chipType = initialState;

const r_chipType = createSlice({
    name: "r_chipType",
    initialState,
    reducers: {
        setChipType(state, action) {
            state = action.payload;
            setReactGlobalVariables("g_chipType", state);
            return state;
        }
    }
});

export const { setChipType } = r_chipType.actions;
export default r_chipType.reducer;
