// g_snippetName
import { createSlice } from "@reduxjs/toolkit";
import { setReactGlobalVariables } from "../../content-web-commonLib";

const initialState = "";

const r_snippetName = createSlice({
    name: "r_snippetName",
    initialState,
    reducers: {
        setSnippetName(state, action) {
            state = action.payload;
            setReactGlobalVariables("g_snippetName", state);
            return state;
        }
    }
});

export const { setSnippetName } = r_snippetName.actions;
export default r_snippetName.reducer;
