import React from 'react'
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from 'react-i18next';
import {
	Form,
	Select,
} from 'antd';

// import { DEFAULT_VALUE } from '../content-web-constants.js';
import {
	initializeAll,
// 	replacejscssfile,
// 	chipSeriesToChipType,
} from '../content-web-commonLib.js';
// import { setChipType } from "../store/slices/r_chipType";
import { set_partNumber_package } from "../store/slices/r_partNumber_package";
import { set_partNumber_type } from "../store/slices/r_partNumber_type";
import { setSnippetName } from "../store/slices/r_snippetName";
import { set_cfg_perFunctions } from "../store/slices/r_cfg_perFunctions";

// const { PART_NUMBER } = DEFAULT_VALUE;

const PartNo = (props) => {
	const { t } = useTranslation();
	const r_partNumber_package = useSelector((state) => state.r_partNumber_package);
	const dispatch = useDispatch();

	// From afterMCUchange()
	const handleChangePartNo = async (value, node) => {
		initializeAll();
		dispatch(set_partNumber_package(value));
		dispatch(set_partNumber_type(node.type));
		dispatch(set_cfg_perFunctions([]));
		dispatch(setSnippetName(""));
	};

	return (
		<Form layout="vertical">

			<Form.Item label={<b>{t("LBL_PART_NO")}</b>}>
				<Select
					defaultValue={r_partNumber_package}
					value={r_partNumber_package}
					onChange={handleChangePartNo}
				>
					{/* From buildMCUselect() */}
					{ NUTOOL_PER.g_cfg_chips.map((g_cfg_chip) => 
						<Select.Option
							key={g_cfg_chip.name || ""}
							value={g_cfg_chip.name + "(" + g_cfg_chip.pkg+")"}
							type={g_cfg_chip.type || ""}
						>
							{g_cfg_chip.name + "(" + g_cfg_chip.pkg+")"}
						</Select.Option>
					)}
				</Select>
			</Form.Item>
		</Form>
	);
};

export default PartNo;