// src/i18n.js
import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import en from './i18n/en.json';
import cn from './i18n/zh-CN.json';
import tw from './i18n/zh-TW.json';
import { LANG_VALUE_TO_KEY } from './content-web-constants';

const resources = {
  'en': {
    translation: en,
  },
  'zh-CN': {
    translation: cn,
  },
  'zh-TW': {
    translation: tw,
  },
};

let defaultLng = LANG_VALUE_TO_KEY[localStorage.getItem("UIlanguage")] || 'en';

i18n.use(initReactI18next).init({
  resources,
  lng: defaultLng,       //預設語言
  fallbackLng: 'en',     //如果當前切換的語言沒有對應的翻譯則使用這個語言，
  interpolation: {
    escapeValue: false,
  },
});

export default i18n;