import React, { Component } from 'react'
import { Tooltip } from 'antd';
import './MenuBtn.css';

class MenuBtn extends Component {
	constructor(props){
		super(props)
		this.state ={}
	};
	render(){
		const { id, src, alt, onClick, hidden } = this.props;

		return (
			<Tooltip title={alt} className="nu-menu-button">
				<img
					id={id}
					src={src}
					alt={alt}
					onClick={onClick}
					hidden={hidden}
					style={{
						boxSizing: "initial",
						width:"48px"
					}}
				/>
			</Tooltip>
		)
	}
}

export default MenuBtn;;