import React, { Component } from 'react'
import { connect } from 'react-redux'
import {
	decidePerControlsV2,
	chipSeriesToChipType,
} from '../content-web-commonLib.js';

import PeripheralConfigure from './PeripheralConfigure.js';

class LoadPeripheralTxt extends Component {
	constructor(props){
		super(props)
		this.state ={
			txtContext: "",
		}
	};

	componentDidMount() {
		const { r_chipType, r_partNumber_package, r_snippetName } = this.props;
		if(r_snippetName){
			this.ReadFile(r_chipType, r_partNumber_package, r_snippetName);
		}
	};

	componentDidUpdate(prevProps) {
		const { r_chipType, r_partNumber_package, r_snippetName } = this.props;
		if (r_snippetName && r_snippetName !== prevProps.r_snippetName){
			if (!g_perControls[r_snippetName]){
				this.ReadFile(r_chipType, r_partNumber_package, r_snippetName);
			}
			// else {
			// 	// Part No.'s type may be changed.
			// 	decidePerControlsV2(
			// 		this.state.txtContext,
			// 		r_partNumber_package,
			// 		r_snippetName,
			// 		() => this.forceUpdate()
			// 	)
			// }
		}
		EXPORT_REACT._D("LoadPeripheralTxt componentDidUpdate");
	};

	componentWillUnmount() { 
		// debugger;
	};

	ReadFile( chipType, partNumber_package, snippetName) {
		chipType = chipSeriesToChipType(chipType);

		var filePath= "PeripheralConfigure\\" + chipType + "\\" + snippetName + '.txt';
		// [Workaround] app.asar.unpacked
		var cwd = window.__dirname || "";
		if(cwd.includes("\\app.asar.unpacked\\")){
			filePath = cwd + "\\" + filePath;
		}

		fetch(`${filePath}`)
			.then(response => response.text())
			// .then(text =>
			// 	g_module_snippet_txt[snippetName] = text
			// )
			.then(text =>
				this.setState({
					txtContext: text.replace(/\r/g, "").replace(/\/\*[\s\S]*?\*\/|\/\/.*/g, '').replace(/<br>/g, ""),
				},() => {
					decidePerControlsV2(
						this.state.txtContext,
						partNumber_package,
						snippetName,
						() => this.forceUpdate()
					)
				})
			)
	};

	render(){
		const { r_snippetName } = this.props;
		var local_perControls = g_perControls[r_snippetName];

		return <PeripheralConfigure local_perControls={local_perControls} />
	}
}

const mapStateToProps = state => ({
	r_chipType: state.r_chipType,
	r_partNumber_package: state.r_partNumber_package,
	r_partNumber_type: state.r_partNumber_type, 
	r_snippetName: state.r_snippetName,
});

export default connect(
	mapStateToProps,
)(LoadPeripheralTxt);;
